import React, { Component } from 'react'
import CardList from '../components/CardList'
import Searchbox from '../components/Searchbox'
import './App.css'
import Scroll from '../components/Scroll'
import ErrorBoundary from '../components/ErrorBoundary'

class App extends Component {

    constructor() {
        super();
        this.state = {
            searchfield: "",
            currentRobots: []
        };
    }

    componentDidMount() {
        // Make API call here and fetch the list of robots to search
        var res = fetch("https://jsonplaceholder.typicode.com/users").then(response => response.json());
        res.then(users => this.setState({ currentRobots: users }));
        console.log(res);
    }

    render() {
        const { searchfield, currentRobots } = this.state;

        let filteredRobots = currentRobots.filter((currentRobot) => {
            return currentRobot.name.toLowerCase().includes(searchfield.toLowerCase());
        });

        return !currentRobots.length ? (<h1 className="tc">Loading state......</h1>) :
            (
                <div className="tc">
                    <h1 className="f2">Robofriends</h1>
                    <Searchbox searchChanged={this.onSearchChanged} />
                    <Scroll>
                        <ErrorBoundary>
                            <CardList robots={filteredRobots} />
                        </ErrorBoundary>
                    </Scroll>
                </div>
            );
    }

    onSearchChanged = (event) => {
        this.setState({ searchfield: event.target.value.trim() });
    }
}

export default App